<template>
	<div>
		<div>
			<h2>Usage</h2>
			<p>In order to get a clothes recommendation, you will have to pick from the above table the clothes that you liked. 
				After that process, you can press the button to get a recommendation.
			</p>
		</div>
		<div>
			<h2>Application</h2>
			<div v-if="this.recomendation">
				<b-button class="boto" variant="danger" @click="getRecomendation()">I don't like this s*** recommendation, let me try again!</b-button>
				<b-row class="row2">
					<b-col cols="3">
					</b-col>
					<b-col cols="6">
						<b-img class="img1" thumbnail src="../assets/camisetaazul.png"></b-img>
					</b-col>
				</b-row>
				<b-row class="row1" align-v="center" style="table-layout=fixed;">
					<b-table v-if="true" stacked :items="this.taulaProperties.recomendation" :fields="this.taulaProperties.fieldsRecomendation">
					</b-table>
					<b-button class="boto" variant="success" target="__blank" href="https://www.amazon.com">Buy!</b-button>
					<b-button class="boto" variant="danger" @click="nextRecommendation()">Next</b-button>
				</b-row>
			</div>
			<div v-else>
				<b-row class="row1">
					<b-button class="boto" variant="success" @click="getRecomendation()">Get A Recommendation!</b-button>
					<b-button class="boto" variant="dark" @click="resetEverything()">Reset Everything</b-button>
				</b-row>
				<h3>Tondir but for clothes</h3>
				<b-row class="row1">
					<div>
						<b-form-select
							class="form-select1"
							v-model="selectForm.sizeSelected"
							title="By not choosing, this app will generate clothes of all sizes."
							:options="this.selectForm.size"
						>
							<template #first>
								<b-form-select-option :value="null" disabled>-- Choose Your Size --</b-form-select-option>
							</template>
						</b-form-select>
					</div>
				</b-row>
				<b-row class="row2" align-v="center">
					<b-col cols="3">
					</b-col>
					<b-col cols="6">
						<b-img class="img1" thumbnail src="../assets/camisetaazul.png"></b-img>
					</b-col>
				</b-row>
				<b-row class="row2">
					<b-col>
						<b-table v-if="true" stacked :items="this.taulaProperties.question" :fields="this.taulaProperties.fieldsQuestion">
						</b-table>
						<b-button class="boto" variant="success" @click="addToPicked()">Like</b-button>
						<b-button class="boto" variant="danger" @click="nextQuestion()">Next</b-button>
					</b-col>
				</b-row>
				<h3>Your liked clothes</h3>
				<b-button class="boto" variant="primary" @click="resetLikes()">Reset My Likes</b-button>
				<b-row class="row1" align-v="center">
					<b-col>
						<b-table class="taulaPicked" v-if="true" :items="this.taulaProperties.pickedClothes" :fields="this.taulaProperties.fieldsPicked">
						</b-table>
					</b-col>
				</b-row>
			</div>
		</div>
	</div>
</template>

<script>

export default {
  name: 'HelloWorld',
  data(){
	  return{
		  recomendation: false,
			taulaProperties: {
				numberOfTotalClothes: 251,
				numberOfQuestionClothes: 50,
				numberOfAnswerClothes: 201,
				clothes: [],
				questionClothes: [],
				question: [],
				answerClothes: [],
				pickedClothes: [],
				preferred: new Map(),
				punctuated: new Map(),
				numberOfRecomendation: 1,
				recomendation: [],
				recomendationClothes: [],
				fieldsQuestion: ['size', 'color', 'buttons', 'hood', 'neck', 'sleeve', 'brand'],
				fieldsPicked: ['size', 'color', 'buttons', 'hood', 'neck', 'sleeve', 'brand'],
				fieldsRecomendation: [{key: 'top', label: 'TOP'}, 'size', 'color', 'buttons', 'hood', 'neck', 'sleeve', 'brand'],
			},
			selectForm: {
				sizeSelected: null,
				size: ["XS" , "S", "M", "L", "XL"],
			},
			clothesAtributes: {
				colors: ["Red", "Blue", "Green", "Yellow", "Purple", "Black"],
				buttons: ["Buttons", "No Buttons"],
				size: ["XS" , "S", "M", "L", "XL"],
				hood: ["Hood", "No Hood"],
				neck: ["Peak", "Circular", "Shirt"],
				sleeve: ["Sleeve", "No sleeve"],
				brand: ["Zora", "Mongo", "Xampion", "Mike", "Adedos"]
			}
	  }
  },
  methods: {
		createClothes: function(){
			var colorSize = this.clothesAtributes.colors.length;
			var buttonsSize = this.clothesAtributes.buttons.length;
			var sizeSize = this.clothesAtributes.size.length;
			var hoodSize = this.clothesAtributes.hood.length;
			var neckSize = this.clothesAtributes.neck.length;
			var sleeveSize = this.clothesAtributes.sleeve.length;
			var brandSize = this.clothesAtributes.brand.length;
			if(this.selectForm.sizeSelected != null){
				for (var i = 0; i < this.taulaProperties.numberOfTotalClothes; ++i){
					var cloth1 = {};
					cloth1.color = this.clothesAtributes.colors[Math.floor(Math.random() * colorSize)];
					cloth1.buttons = this.clothesAtributes.buttons[Math.floor(Math.random() * buttonsSize)];
					cloth1.size = this.selectForm.sizeSelected;
					cloth1.hood = this.clothesAtributes.hood[Math.floor(Math.random() * hoodSize)];
					cloth1.neck = this.clothesAtributes.neck[Math.floor(Math.random() * neckSize)];
					cloth1.sleeve = this.clothesAtributes.sleeve[Math.floor(Math.random() * sleeveSize)];
					cloth1.brand = this.clothesAtributes.brand[Math.floor(Math.random() * brandSize)];
					cloth1.like = "Like It";
					this.taulaProperties.clothes.push(cloth1);
				}
			}
			else{
				for (var j = 0; j < this.taulaProperties.numberOfTotalClothes; ++j){
					var cloth2 = {};
					cloth2.color = this.clothesAtributes.colors[Math.floor(Math.random() * colorSize)];
					cloth2.buttons = this.clothesAtributes.buttons[Math.floor(Math.random() * buttonsSize)];
					cloth2.size = this.clothesAtributes.size[Math.floor(Math.random() * sizeSize)];
					cloth2.hood = this.clothesAtributes.hood[Math.floor(Math.random() * hoodSize)];
					cloth2.neck = this.clothesAtributes.neck[Math.floor(Math.random() * neckSize)];
					cloth2.sleeve = this.clothesAtributes.sleeve[Math.floor(Math.random() * sleeveSize)];
					cloth2.brand = this.clothesAtributes.brand[Math.floor(Math.random() * brandSize)];
					cloth2.like = "Like It";
					this.taulaProperties.clothes.push(cloth2);
				}
			}
			this.taulaProperties.questionClothes = this.taulaProperties.clothes.slice(0,this.taulaProperties.numberOfQuestionClothes);
			this.taulaProperties.answerClothes = this.taulaProperties.clothes.slice(this.taulaProperties.numberOfQuestionClothes+1,this.taulaProperties.numberOfTotalClothes);
			this.taulaProperties.question.push(this.taulaProperties.questionClothes.shift());
		},
		addToPicked: function(){
			var item = this.taulaProperties.question[0];
			if(this.taulaProperties.pickedClothes.indexOf(item) == -1){
				this.taulaProperties.pickedClothes.push(item);
				this.taulaProperties.questionClothes.splice(this.taulaProperties.questionClothes.indexOf(item), 1);
				for (var [, key] of Object.entries(item)) {
					if(this.taulaProperties.preferred.has(key)){
						let value = this.taulaProperties.preferred.get(key);
						this.taulaProperties.preferred.set(key, value+1)
					}
					else{
						this.taulaProperties.preferred.set(key, 1);
					}
				}
			}
			this.nextQuestion();
		},
		nextQuestion: function(){
			if(this.taulaProperties.questionClothes.length == 0){
				this.getNewClothes();
			}
			this.taulaProperties.question.pop();
			this.taulaProperties.question.push(this.taulaProperties.questionClothes.shift());
		},
		nextRecommendation: function(){
			if(this.taulaProperties.numberOfRecomendation >= this.taulaProperties.recomendationClothes.length){
				this.taulaProperties.numberOfRecomendation = 0;
			}
			this.taulaProperties.numberOfRecomendation = this.taulaProperties.numberOfRecomendation+1;
			var cloth = this.taulaProperties.recomendationClothes[this.taulaProperties.numberOfRecomendation-1];
			cloth.top = this.taulaProperties.numberOfRecomendation;
			this.taulaProperties.recomendation.pop();
			this.taulaProperties.recomendation.push(cloth);
		},
		punctuate: function(){
			for (var i = 0; i < this.taulaProperties.answerClothes.length; ++i) {
				var punctuation = 0;
				for (var [, key] of Object.entries(this.taulaProperties.answerClothes[i])){
					if(this.taulaProperties.preferred.has(key)){
						if(this.clothesAtributes.brand.indexOf(key) != -1){
							punctuation = punctuation + this.taulaProperties.preferred.get(key)*3;
						}
						else{
							punctuation = punctuation + this.taulaProperties.preferred.get(key);
						}
					}
				}
				this.taulaProperties.punctuated.set(this.taulaProperties.answerClothes[i], punctuation);
			}
		},
		sortByPunctuation: function(){
			var aux = [];
			this.taulaProperties.punctuated[Symbol.iterator] = function* () {
				yield* [...this.entries()].sort((a, b) => a[1] - b[1]);
			}
			for (let [key,] of this.taulaProperties.punctuated) {
				aux.push(key);
			}
			this.taulaProperties.recomendationClothes = aux;
			var cloth = this.taulaProperties.recomendationClothes[this.taulaProperties.numberOfRecomendation-1];
			cloth.top = this.taulaProperties.numberOfRecomendation;
			this.taulaProperties.recomendation.push(cloth);
		},
		resetClothes: function(){
			this.taulaProperties.clothes = [];
			this.taulaProperties.questionClothes = [];
			this.taulaProperties.answerClothes = [];
			this.taulaProperties.question = [];
		},
		resetEverything: function(){
			this.getNewClothes();
			this.resetLikes();
		},
		getRecomendation: function(){
			if(!this.recomendation){
				this.taulaProperties.recomendation = [];
				this.taulaProperties.numberOfRecomendation = 1;
				this.punctuate();
				this.sortByPunctuation();
			}
			this.recomendation = !this.recomendation;
		},
		getNewClothes: function(){
			this.resetClothes();
			this.createClothes();
		},
		resetLikes: function(){
			this.taulaProperties.pickedClothes = [];
			this.taulaProperties.preferred = new Map();
			this.taulaProperties.punctuated = new Map();
			this.taulaProperties.recomendationClothes = [];
		}
  },
  watch: {
    'selectForm.sizeSelected': function () {
      this.resetEverything();
    }
  },
  created: function(){
	  this.createClothes();
  }
}
</script>

<style scoped>
.row1 {
	display: block;
	margin-right: 0px;
	padding-bottom: 10px;
}

.row2 {
	margin-right: 0px;
	padding-bottom: 10px;
}

.img1 {
	max-width: 200px;
	max-height: 200px;
}

.form-select1{
	width: auto;
}

table { 
    table-layout:fixed;
    word-wrap:break-word;
}

.boto {
	margin: 5px;
}
</style>
